import chainIcon from "@/assets/chainIcon.png";
import gangtanhao from "@/assets/gangtanhao.png";
import san from "@/assets/san.png";
import arrs from "@/assets/arrs.png";
import Security from "../component/security";
import isIDCard from "../component/isIDCard";
export default {
  data() {
    return {
      isShowIdCard: false,
      showPositionValue: false, //提示信息
      toastContent: "",
      san: san,
      arrs: arrs,
      topTitle: "提币",
      chainIcon: chainIcon,
      gangtanhaoIcon: gangtanhao,
      list: [],
      note: "",
      minWithdrawAmount: "",
      accountNum: 0,
      form: {
        coin_type: "",
        chain: "0",
        coin_status: "",
        minNumber: "0",
        fee_amount: "0",
        amount: "",
        address: "",
        memo: ""
      },
      isSecurity: false //登陆密码弹窗
    };
  },
  components: { "v-security": Security, "v-isIDCard": isIDCard },
  filters: {
    uppercase: function(value) {
      if (!value) {
        return "";
      }
      return value.toUpperCase();
    }
  },
  mounted() {
    if (this.form.amount == undefined) {
      this.accountNum = 0;
    }
    this.getCoin();
  },
  methods: {
    // /获取币种币链
    getCoin() {
      var that = this,
        formData = {
          sign: that.$store.state.user.sign,
          access_token: that.$store.state.user.token
        };
      that
        .$request({
          method: "POST",
          url: that.$baseURL + "/wallet/get-coin",
          data: that.$qs.stringify(formData)
        })
        .then(function(response) {
          that.list = response.data;
          if (that.$route.params.id) {
            that.form = that.$route.params.id;
            that.getAmount();
          } else {
            if (that.$route.params.coin_type) {
              that.form.coin_type = that.$route.params.coin_type;
            } else {
              that.form.coin_type = that.list[0].coin_type;
            }
            that.getChainCoin(that.form.coin_type);
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    getChainCoin(val) {
      var that = this,
        formData = {
          sign: that.$store.state.user.sign,
          access_token: that.$store.state.user.token,
          coin_type: val
        };
      that
        .$request({
          method: "POST",
          url: that.$baseURL + "/wallet/get-chain-coin",
          data: that.$qs.stringify(formData)
        })
        .then(function(response) {
          // that.form.alias_name = response.data[0].alias_name;
          that.form.chain = response.data[0].chain;
          that.form.coin_status = response.data[0].coin_status;
          that.getAmount();
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    // 获取手续费和最小提币数量
    getAmount() {
      var that = this,
        formData = {
          sign: that.$store.state.user.sign,
          access_token: that.$store.state.user.token,
          chain: that.form.chain,
          coin_type: that.form.coin_type
        };
      that
        .$request({
          method: "POST",
          url: that.$baseURL + "/wallet/withdraw-fee-amount",
          data: that.$qs.stringify(formData)
        })
        .then(function(response) {
          that.minWithdrawAmount = response.data.min_withdraw_amount;
          that.form.fee_amount = response.data.fee_amount;
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    getInpout() {
      var that = this;
      if (that.form.amount == undefined) {
        that.accountNum = 0;
      } else {
        that.accountNum =
          Number(that.form.amount) - Number(that.form.fee_amount);
      }
    },
    // 提交
    getSubmit() {
      var that = this,
        pattrn = /^\d+(\.\d+)?$/g; //&& !pattrn.test(that.form.amount)
      if (that.form.address == "") {
        that.showPositionValue = true;
        that.toastContent = "地址不可为空";
      } else if (that.form.amount == undefined || that.form.amount == "") {
        that.showPositionValue = true;
        that.toastContent = "数量不可为空且为正数";
      } else {
        var formData = {
          sign: that.$store.state.user.sign,
          access_token: that.$store.state.user.token,
          chain: that.form.chain,
          coin_type: that.form.coin_type,
          address: that.form.address
        };
        // 检测是否是内部地址
        that
          .$request({
            method: "POST",
            url: that.$baseURL + "/wallet/internal-addr",
            data: that.$qs.stringify(formData)
          })
          .then(function(response) {
            // if (response.code == 204) {//TODO
            //   that.showPositionValue = true;
            //   that.toastContent = response.errmsg;
            // } else {
            that.getISIDcard();
            // }
          })
          .catch(function(error) {
            console.log(error);
          });
      }
    },
    getSubmitSecurity() {
      var that = this,
        formData = {
          sign: that.$store.state.user.sign,
          access_token: that.$store.state.user.token,
          chain: that.form.chain,
          coin_type: that.form.coin_type,
          address: that.form.address,
          amount: that.form.amount
        };
      that
        .$request({
          method: "POST",
          url: that.$baseURL + "/wallet/withdraw-submit",
          data: that.$qs.stringify(formData)
        })
        .then(function(response) {
          if (response.code == 0) {
            that.showPositionValue = true;
            that.toastContent = "提币成功";
            that.isSecurity = false;
            that.form.address = "";
            that.form.minNumber = "";
            that.form.amount = "";
            that.form.fee_amount = "0";
            // 跳转到我的钱包-对应币种详情页面
            //  that.$router.push({
            //   name: "walletDetail",
            //   params: {
            //     id: that.$route.params.id
            //   }
            // });
          } else {
            that.showPositionValue = true;
            that.toastContent = response.errmsg;
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    // 余额数
    getAll(val) {
      var that = this,
        formData = {
          sign: that.$store.state.user.sign,
          access_token: that.$store.state.user.token
        };
      that
        .$request({
          method: "POST",
          url: that.$baseURL + "/wallet/balance-detail",
          data: that.$qs.stringify(formData)
        })
        .then(function(response) {
          if (val == "usdt") {
            that.form.amount = response.data.usdt_eth;
          } else if (val == "cnc") {
            that.form.amount = response.data.cnc_bts;
          } else if (val == "btc") {
            that.form.amount = response.data.btc_btc;
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    // 提币地址页面
    getArrs() {
      // this.$router.push("/wallet/address");
      this.$router.push({
          name: "walletAddress",
          params: {
            coin_type: this.form.coin_type
          }
        });

    },
    // 登陆密码弹窗关闭
    closeFun() {
      this.isSecurity = false;
    },
    // 币链接信息介绍
    getInfo() {
      this.$router.push("/chainCoinInfo");
    },
    // 是否身份认证组件
    getISIDcard() {
      var that = this,
        formData = {
          sign: that.$store.state.user.sign,
          access_token: that.$store.state.user.token
        };
      that
        .$request({
          method: "POST",
          url: that.$baseURL + "/personal/is-real-name",
          data: that.$qs.stringify(formData)
        })
        .then(function(response) {
          if (response.code == 204) {
            that.isShowIdCard = true;
          } else {
            that.isSecurity = true;
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    //
    setCloseIdCard() {
      this.isShowIdCard = false;
    }
  }
};



